<template>
  <v-row>
    <v-col class="py-0 px-5">
      <v-card elevation="0">
        <v-card-title class="font-weight-regular">Авторизация</v-card-title>
        <v-card-text>Чтобы продолжить, пожалуйста авторизуйтесь</v-card-text>
        <v-card-actions>
          <v-btn
            icon
            color="primary"
            @click="openWindow"
            large
          >
            <v-icon>mdi-vk</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
export default {
  methods: {
    openWindow() {
      window.open('http://142.44.243.112:3000/auth/vk/login', '_blank', 'width=800, height=600');
      window.addEventListener('message', event => {
        const { token, success } = event.data;

        if (success) {
          localStorage.token = token;
        };
      });
    }
  }
}
</script>